import { Component, ViewChild, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ProductService } from "../../providers/product.services";
import { environment } from "../../../environments/environment";
import { IonSearchbar } from "@ionic/angular";

@Component({
  selector: "page-orders",
  templateUrl: "orders.html",
  styleUrls: ["./orders.scss"],
})
export class OrdersPage implements OnInit {
  public orderList: any = [];
  public reviewList: any = [];
  public cartCount = 0;
  public refershDefault = {
    target: {
      complete: function () {
        return true
      }
    }
  }

  constructor(private _ProductService: ProductService) {}

  public isSearchEnable: Boolean = false;
  @ViewChild("autofocus", { static: false }) searchbar: IonSearchbar;
  public baseUrl = environment.baseUrl + "/";
  public searchproductList: any = [];

  ngOnInit() {
    this.getorderList();
    this.getCartCount();
  }

  public rating;
  rate(index, id) {
    console.log("called", index, id);

    this.orderList.forEach((order) => {
      if (order._id == id) {
        // this.boxId = id;
        order.rating = index;
        this.rating = index;
      }
    });
  }

  getColor(index: number, rating) {
    if (this.isAboveRating(index, rating)) {
      return "#DDDDDD";
    }

    return "gold";
  }

  isAboveRating(index: number, rating) {
    return index > rating;
  }

  public fetchReviewList() {
    let resp;
    this._ProductService
      .getAllReviewList(localStorage.getItem("userId"))
      .subscribe((response: any) => {
        resp = response;
        this.reviewList = resp;
        console.log(this.reviewList, "reviewList");
        this.mapRatingwithProducts();
      });
  }

  mapRatingwithProducts() {
    this.orderList.forEach((order) => {
      this.reviewList.forEach((list) => {
        if (order.orderId == list.orderId) {
          console.log(
            "order.productId == list.productId",
            order.orderId,
            list.orderId,
            order.orderId == list.orderId
          );
          order.rating = parseInt(list.rating);
        } else {
          order.rating = 0;
        }
      });
    });

    console.log("this.orderList", this.orderList);
  }

  public showSearchBox: Boolean = false;
  enableSearch() {
    this.showSearchBox = true;
    setTimeout(() => this.searchbar.setFocus(), 500);
  }

  backToNormal() {
    this.showSearchBox = false;
  }


  refershOrders(){
    this.resetPage();
    this.getorderList();
  }

  getorderList(event = this.refershDefault) {
    this._ProductService
      .getOrderList(this.page,localStorage.getItem("userId"))
      .subscribe((response: any) => {
        let arr = response["OrderList"];
        if (arr.length > 0) {
          this.orderList = this.uniqueByKeepLast(arr, it=>it.orderId);
        }
        console.log(this.orderList, "orderList");
      });

      if (event) {
        setTimeout(() => {
          console.log('Async operation has ended');
          event.target.complete();
        }, 1500);
      }
  }



  getCartCount() {
    let shopId = localStorage.getItem("shopID");
    this._ProductService
      .cartList(localStorage.getItem("userId"), shopId)
      .subscribe((data: any) => {
        if (data.length !== 0) {
          this.cartCount = data[0].productDetails.length;
        }
      });
  }


  public page = {
    limit: 10,
    skip: 0
  }

  resetPage(){
    this.page={
      limit:10,
      skip:0
    }
  }

  loadOrderData(event) {

    this.page.limit = this.page.limit ;
    this.page.skip = this.page.skip + 10

    this._ProductService.getOrderList(this.page,localStorage.getItem("userId")
      ).subscribe((response: any) => {

      console.log(this.orderList, "orderList")
      setTimeout(() => {
        console.log('Done');
        event.target.complete();
        if(Array.isArray(response)){
          this.orderList = this.orderList.concat(response)
        }else{
          this.orderList = this.orderList.concat(response['OrderList'])
        }
      }, 0);
    });
  }



  uniqueByKeepLast(data, key) {
    return [...new Map(data.map((x) => [key(x), x])).values()];
  }
}
